@import url("https://cdn.jsdelivr.net/gh/fonts-archive/Pretendard/Pretendard.css");
@font-face {
  font-family: "satoshi";
  src: url("./Satoshi-Variable.woff") format("woff");
}

* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  background: #f0f0f0;
}

.alignLeft {
  text-align: left;
}

.alignRight {
  text-align: right;
}

.display {
  display: flex;
  font-family: "satoshi";
  font-style: normal;
  font-weight: 500;
  line-height: 100%; /* 180px */
  font-size: 1.5em;
  height: 100vh;
  max-width: 1376px;
  padding: 0px 40px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 1em;
  /* align-self: stretch; */
  background-color: transparent;
}
.display p {
  width: 100%;
  height: fit-content;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

canvas {
  opacity: 0;
  touch-action: none;
  animation: fade-in 3s ease 0.5s forwards;
}
